.search_input{
    display: flex;
    align-items: center;
    border: 1px solid lightgray;
    height: 30px;
    padding: 10px 20px;
    border-radius: 999px;
    width: 75vh;
    margin: 0 auto;
    margin-top: 40px;
    max-width: 560px;
}
.search_input > input{
    flex: 1;
    padding: 10px 20px;
    font-size: medium;
    border: none;
}
.search_input > input:focus{
    outline-width: 0;
}
.search_inputIcon{
    color: gray;
}
.search_buttons{
    margin-top: 30px;
    display: flex;
    justify-content: center;
}
.search_buttons button{
    margin: 5px;
    padding: 7px 15px;
    text-transform: inherit;
}
.search_buttonsHidden{
    display: none !important
}